%btn {
    @extend .btn;
}

%active {
    @extend %outline-primary;
}

%hover {
    @extend %outline-primary-hover;
}

.menu-header {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    max-height: 100%;
    min-width: 200px;
    max-width: 300px;
    padding: 15px 0;
    overflow-y: auto;
    transform: translateX(-100%);
    background-color: $dark;
    transition: transform .5s;
    z-index: 2000;
    &.active {
        transform: translateX(0);
    }
    .nav {
        &-link {
            display: block;
            font-size: 12px;
            font-weight: 700;
            padding: 7px 50px 7px 30px;
            color: $primary-color;
        }
    }
    .menu-logo {
        display: block;
        margin: 0 0 40px;
        text-align: center;
        .logo-site {
            width: 80px;
            height: 23px;
        }
        @include respond-to($md) {
            display: none;
        }
    }
    .close-nav {
        position: absolute;
        top: 19px;
        left: 21px;
        display: block;
        width: 13px;
        height: 13px;
        border: none;
        background: url("../img/icons/btn_menu_close.svg") center center no-repeat;
        z-index: 10;
        @include respond-to($md) {
            display: none;
        }
    }
}

.nav {
    flex-direction: column;
    &-link {
        &.active {
            border: none !important;
            background-color: #1e2e55;
        }
    }
    @include respond-to($md) {
        flex-direction: row;
        &-item {
            margin: 0 20px;
            &:last-child {
                margin-right: 0;
            }
        }
        &-link {
            @extend %btn;
            font-size: 18px;
            color: $dark;
            border: 3px solid transparent;
            &.active,
            &:active {
                @extend %active;
            }
            &:hover,
            &:focus {
                @extend %hover;
                color: $primary-color !important;
            }
        }
    }
}

@include respond-to($md) {
    .scroll {
        background-color: $dark !important;
        .nav {
            &-link {
                color: #fff;
                &.active,
                &:active {
                    color: $blue;
                }
            }
        }
    }
}

